.b-footer {
  padding: 30px 0;
  background: #000;
  color: #fff;
  font-size: 20px;
  @include r(959) {
    font-size: 16px;
  }
  @include r(767) {
    padding-bottom: 10px;
  }
  .l {
    display: flex;
    justify-content: space-between;
    align-items: center;
    @include r(767) {
      display: block;
      text-align: center;
      font-size: 0;
    }
  }
  &__col {
    @include r(1300) {
      &:first-of-type {
        display: none;
      }
    }
    @include r(767) {
      font-size: 16px;
      width: 50%;
      display: inline-block;
      vertical-align: middle;
      margin-bottom: 20px;
    }
    @include r(479) {
      width: 100%;
      &:first-of-type {
        display: block;
      }
    }
  }
  &__contact {
    display: table;
    margin: 10px 0;
    color: #fff;
    text-decoration: none;
    @include r(767) {
      margin: 10px auto;
    }
    svg {
      color: $mainColor;
      margin-right: 10px;
    }
  }
  .b-socials {
    &__link {
      @include r(1300) {
        margin: 5px;
      }
    }
  }
}

.b-schedule {
  @include r(767) {
    display: table;
    margin: 0 auto;
  }
  &__row {
    width: 180px;
    display: flex;
    justify-content: space-between;
    @include r(959) {
      width: 140px;
    }
  }
}

.popup {
  &__title {
    margin-bottom: 20px;
    color: #000;
    font-size: 26px;
    text-align: center;
    @include r(479) {
      font-size: 22px;
    }
  }
  &-thanks {
    padding: 30px;
    max-width: 375px;
    margin: 0 auto;
    position: relative;
    text-align: center;
    background-color: #fff;
    img {
      display: block;
      margin: 0 auto 15px;
      width: 109px;
      height: 109px;
    }
    &__title {
      margin-bottom: 10px;
      color: #000;
      font-size: 30px;
      font-weight: 500;
    }
    &__text {
      color: #000;
      font-size: 15px;
      font-weight: 400;
      line-height: 22px;
    }
  }
}//popup