.js-btn {
  -ms-touch-action: manipulation;
  touch-action: manipulation;
}

.btn {
  display: inline-block;
  font-family: $buttonFont;
  font-size: 18px;
  line-height: 1.2;
  font-weight: 400;
  color: #fff;
  vertical-align: middle;
  text-align: center;
  text-decoration: none;
  white-space: nowrap;
  background-color: transparent;
  border: none;
  outline: none;
  transition: all .3s;
  user-select: none;
  -ms-touch-action: manipulation;
  touch-action: manipulation;
  &:hover {
    cursor: pointer;
  }
  & > .svg {
    display: inline-block;
    margin-right: 1px;
    vertical-align: -1px;
  }
  &--std {
    position: relative;
    min-width: 380px;
    padding: 18px 35px;
    font-size: 24px;
    text-transform: uppercase;
    background-color: #00a651;
    border-radius: 50px;
    @include r(479) {
      font-size: 16px;
      min-width: 0;
    }
    &:hover {
      background-color: darken($mainColor, 5%);
    }
  }//btn--std
}//btn