@import "../components/layout";
@import "../components/button";
@import "../components/input";

.b-slider {
  position: relative;
  user-select: none;
  &__pagination {
    position: absolute;
    z-index: 1;
    bottom: 20px;
    left: 0;
    right: 0;
    text-align: center;
    .swiper-pagination-bullet {
      margin: 0 3px;
      width: 12px;
      height: 12px;
      opacity: 1;
      background-color: #e1e1e1;
      transition: all .3s;
      &.swiper-pagination-bullet-active {
        background-color: $mainColor;
      }
    }
  }
  &__prev, &__next {
    position: absolute;
    top: 50%;
    z-index: 1;
    width: 40px;
    height: 40px;
    margin-top: -20px;
    cursor: pointer;
    color: #ababab;
    transition: all .3s;
    display: flex;
    align-items: center;
    justify-content: center;
    &:hover {
      color: $mainColor
    }
  }
  &__prev {
    left: 0;
  }
  &__next {
    right: 0;
  }
}