body {
	min-width: 320px;
	max-width: 1920px;
	margin: 0 auto;
	font-family: $mainFont;
	font-weight: 400;
	font-size: 16px;
	color: #000;
	background-color: #fff;
	position: relative;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

h1, h2, h3, h4, h5, h6, p {
	margin: 0;
	padding: 0;
}

img {
	max-width: 100%;
	height: auto;
}

svg {
	fill: currentColor;
}

.is-hidden {
	display: none;
}