.b-field {
	$p: &;
	position: relative;
	margin-top: 15px;
	color: #000;
	font-size: 18px;
	text-align: left;
	letter-spacing: 0.015em;
	&.is-filled {
		#{$p}__label, .field-suffix {
			font-size: 16px;
			transform: translateY(-20px);
		}
	}
	input:not([type="submit"]), textarea, select {
		width: 100%;
		padding: 10px 25px;
		color: inherit;
		text-align: inherit;
		background-color: #fff;
		border: none;
		border-bottom: 1px solid #cdd7e1;
		border-radius: 0;
		outline: none;
		transition: all 0.4s;
		font-family: $mainFont;
		line-height: 1.2;
		@include r(479) {
			padding: 10px;
		}
		@include placeholder {
			line-height: 1.2;
			opacity: 1;
			color: #969696;
			transition: opacity 0.5s;
			font-family: $mainFont;
		}
		&:focus {
			@include placeholder {
				opacity: 0;
			}
		}
	}
	textarea {
		resize: none;
		height: 100px;
	}
	&.has-error {
		input:not([type="submit"]) {
			color: #ff230c;
			border-color: #ff230c;
		}
		#{$p}__error {
			//opacity: 1;
			//z-index: 1;
			//transform: scale(1);
			//transition: opacity 0.5s ease, transform 0.5s ease, z-index 0s;
		}
	}
	&__label {
		position: absolute;
		left: 25px;
		top: 6px;
		color: #708ba9;
		font-size: 18px;
		pointer-events: none;
		transition: all .3s;
		@include r(479) {
			left: 10px;
		}
	}
	&__error {
		position: absolute;
		top: 100%;
		margin-top: 10px;
		color: #ff3131;
		font-size: 15px;
		font-weight: 400;
		text-align: left;
		user-select: none;
		cursor: default;
	}
}//b-field