.b-header {
  position: relative;
  padding-left: 100px;
  padding-right: 100px;
  background: url($img + 'header-bg.jpg') no-repeat center center;
  background-size: cover;
  height: 1100px;
  @include r(1300) {
    height: 880px;
  }
  @include r(959) {
    padding-left: 0;
    padding-right: 0;
  }
  @include r(767) {
    padding-bottom: 5px;
    height: auto;
    margin-bottom: 30px;
  }
  & > .l {
    max-width: 1440px;
  }
  &::before {
    content: '';
    position: absolute;
    top: 0; right: 0; bottom: 0; left: 0;
    z-index: 0;
    background-color: rgba(#000, 0.4);
  }
  &__top {
    position: relative;
    z-index: 1;
    display: flex;
    justify-content: space-between;
    padding: 35px 0;
    margin-bottom: 50px;
    @include r(767) {
      padding: 15px 0;
      margin-bottom: 0;
      display: block;
      text-align: center;
    }
  }
  &__middle {
    position: relative;
    z-index: 1;
    text-align: center;
    @include r(959) {
      padding-left: 70px;
      padding-right: 70px;
    }
    @include r(767) {
      padding: 0;
    }
    .b-logo {
      margin-bottom: 55px;
      @include r(767) {
        margin-bottom: 20px;
      }
    }
  }
  &__title {
    margin-bottom: 10px;
    color: #fff;
    font-size: 60px;
    text-transform: uppercase;
    @include r(767) {
      font-size: 30px;
    }
  }
  &__subtitle {
    margin-bottom: 35px;
    opacity: 0.8;
    color: #fff;
    font-size: 30px;
    @include r(767) {
      font-size: 24px;
      margin-bottom: 15px;
    }
  }

  .b-socials {
    position: absolute;
    left: 15px;
    top: 20%;
    z-index: 1;
    display: table;
    @include r(959) {
      left: 0;
    }
    @include r(767) {
      position: relative;
      top: auto;
      left: auto;
      margin: 0 auto 30px;
    }
    &__title {
      position: absolute;
      top: 100%;
      left: 0;
      padding-right: 70px;
      display: inline-block;
      font-size: 24px;
      text-transform: uppercase;
      white-space: nowrap;
      transform: rotate(-90deg) translate(-100%, 100%);
      transform-origin: left top;
      user-select: none;
      @include r(767) {
        display: none;
      }
      &::after {
        content: '';
        position: absolute;
        right: 0;
        top: 50%;
        width: 58px;
        height: 1px;
        background-color: #fff;
      }
    }
    &__list {
      display: inline-block;
    }
    &__link {
      display: table;
      @include r(767) {
        display: inline-block;
        margin: 5px 20px;
      }
    }
  }
}//b-header

.b-socials {
  color: #fff;
  cursor: default;
  &__link {
    display: inline-block;
    margin: 20px;
    color: inherit;
    text-decoration: none;
    transition: all .3s;
    &:hover {
      color: $mainColor;
    }
  }
}//b-socials

.b-menu {
  display: flex;
  flex-grow: 1;
  justify-content: space-around;
  @include r(767) {
    display: block;
    margin-bottom: 10px;
  }
  &__link {
    color: #fff;
    font-size: 24px;
    text-transform: uppercase;
    text-decoration: none;
    transition: all .3s;
    @include r(1300) {
      font-size: 16px;
    }
    @include r(767) {
      display: inline-block;
      margin: 5px;
      font-size: 14px;
    }
    &:hover {
      color: $mainColor;
    }
  }
}//b-menu

.b-phone {
  margin-left: 30px;
  color: #fff;
  font-size: 24px;
  text-transform: uppercase;
  text-decoration: none;
  transition: all .3s;
  @include r(1300) {
    margin-left: 0;
    font-size: 16px;
  }
  @include r(767) {
    font-size: 24px;
  }
  &:hover {
    color: $mainColor;
  }
}

.b-youtube {
  position: absolute;
  right: 40px;
  bottom: 20px;
  z-index: 10;
  color: #fff;
  font-size: 20px;
  text-transform: uppercase;
  text-decoration: none;
  transition: all .3s;
  @include r(1800) {
    bottom: 300px;
  }
  @include r(1300) {
    bottom: 220px;
  }
  @include r(767) {
    display: table;
    position: relative;
    bottom: auto;
    right: auto;
    margin: 20px auto;
  }
  &:hover {
    color: $mainColor;
  }
  svg {
    display: inline-block;
    margin-left: 10px;
    vertical-align: -1px;
  }
}

.b-play {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100px;
  height: 100px;
  border: 2px solid #fff;
  border-radius: 50%;
  color: #fff;
  transition: all .3s;
  svg {
    display: block;
    transform: translateX(4px);
  }
}

.b-video-block {
  position: relative;
  z-index: 1;
  margin-top: -250px;
  padding-left: 150px;
  padding-right: 150px;
  @include r(1300) {
    margin-top: -200px;
  }
  @include r(767) {
    margin-top: 0;
    padding: 0;
  }
  &__title {
    margin-bottom: 20px;
    color: #fff;
    font-size: 30px;
    font-weight: 500;
    text-transform: uppercase;
    text-align: center;
    @include r(959) {
      font-size: 20px;
    }
    @include r(767) {
      color: #000;
      font-size: 16px;
      margin-bottom: 10px;
    }
  }
}//b-video-block

.b-video {
  position: relative;
  z-index: 1;
  display: block;
  width: 1200px;
  max-width: 100%;
  margin: 0 auto;
  @include r(767) {
    margin-bottom: 20px;
  }
  &:hover {
    .b-play {
      color: $mainColor;
      border-color: $mainColor;
    }
  }
  &::before {
    content: '';
    position: absolute;
    top: 0; right: 0; bottom: 0; left: 0;
    z-index: 0;
    background-color: rgba(#000, 0.4);
  }
  & > img {
    display: block;
  }
  iframe {
    position: absolute;
    top: 0; right: 0; bottom: 0; left: 0;
    z-index: 5;
    width: 100%;
    height: 100%;
    border: none;
  }
  .b-play {
    position: absolute;
    top: 50%;
    left: 50%;
    margin-top: -50px;
    margin-left: -50px;
  }
  &__title {
    position: absolute;
    left: 0; right: 0; bottom: 30px;
    color: #fff;
    font-size: 30px;
    text-transform: uppercase;
    text-align: center;
    @include r(959) {
      font-size: 20px;
      bottom: 10px;
    }
    @include r(767) {
      display: none;
    }
  }
}//b-video

.b-adv {
  margin: -70px auto 40px;
  padding: 100px 190px 20px;
  display: flex;
  justify-content: space-between;
  border: 2px solid #00a651;
  border-image: url($img + 'icons/border.png') 2 repeat repeat;
  @include r(1300) {
    padding-left: 30px;
    padding-right: 30px;
  }
  @include r(767) {
    margin: 0 auto 40px;
    padding: 10px;
    display: block; 
    text-align: center;
    font-size: 0;
  }
  &__item {
    color: #000;
    font-size: 18px;
    text-transform: uppercase;
    text-align: center;
    @include r(959) {
      font-size: 14px;
    }
    @include r(767) {
      display: inline-block;
      vertical-align: top;
      width: 50%;
      margin-bottom: 10px;
      text-align: center;
    }
  }
  &__title {
    margin-bottom: 10px;
    color: #00a651;
    font-size: 60px;
    font-weight: 700;
    @include r(959) {
      font-size: 40px;
    }
  }
}//b-adv

.b-catalog {
  margin-bottom: 50px;
  text-align: center;
  &__title {
    color: #4f4f4f;
    font-size: 30px;
    font-weight: 700;
    text-transform: uppercase;
    @include r(479) {
      font-size: 20px;
      margin-bottom: 10px;
    }
  }
  &__subtitle {
    margin-bottom: 45px;
    color: #9b9b9b;
    font-size: 24px;
    font-weight: 300;
    @include r(479) {
      font-size: 18px;
    }
  }
  &__list {
    max-width: 1420px;
    margin: 0 auto 45px;
    padding: 0 70px;
    @include r(479) {
      padding: 0 35px;
    }
  }
}//b-catalog

.b-item {
  display: inline-block;
  width: 280px;
  max-width: 100%;
  padding: 15px 30px;
  margin-bottom: 20px;
  text-align: center;
  border: 1px solid #dcdcdc;
  &__img {
    img {
      display: block;
      margin: 0 auto 30px;
    }
  }
  &__title {
    color: #828282;
    font-size: 20px;
    font-weight: 300;
  }
  &__price {
    margin-bottom: 15px;
    color: #00a651;
    font-size: 24px;
  }
}//b-item

.b-contract {
  margin-bottom: 60px;
  padding-top: 50px;
  padding-bottom: 70px;
  text-align: center;
  background: url($img + 'contract-bg.jpg') no-repeat center center;
  background-size: cover;
  @include r(479) {
    padding-top: 30px;
    padding-bottom: 30px;
  }
  &__title {
    margin-bottom: 25px;
    color: #fff;
    font-size: 30px;
    line-height: 1.7;
    text-transform: uppercase;
    @include r(959) {
      font-size: 24px;
    }
    @include r(479) {
      font-size: 16px;
    }
  }
  &__text {
    opacity: 0.8;
    color: #fff;
    font-size: 20px;
  }
  &__list {
    margin-bottom: 20px;
    display: flex;
    justify-content: center;
  }
  &__item {
    position: relative;
    display: block;
    margin: 0 25px;
    text-decoration: none;
    @include r(479) {
      margin: 0 5px;
    }
    &:hover {
      &::after {
        transform: scale(1);
      }
    }
    &::after {
      content: '';
      position: absolute;
      top: 0; right: 0; bottom: 0; left: 0;
      background: rgba(#000, 0.4) url($img + 'svg/fullscreen.svg') no-repeat center center;
      background-size: 54px;
      transform: scale(0);
      transition: all .25s;
    }
    img {
      display: block;
    }
  }
}//b-contract

.b-services {
  margin-bottom: 60px;
  text-align: center;
  &__title {
    margin-bottom: 10px;
    color: #00a651;
    font-size: 30px;
    font-weight: 700;
    text-transform: uppercase;
    @include r(479) {
      font-size: 20px;
    }
  }
  &__subtitle {
    margin-bottom: 60px;
    color: #585858;
    font-size: 24px;
    @include r(479) {
      font-size: 18px;
    }
  }
}//b-services

.b-service {
  display: inline-block;
  width: 195px;
  margin: 0 15px;
  color: #545454;
  font-size: 20px;
  font-weight: 300;
  backface-visibility: hidden;
  @media screen and (min-width: 1000px) {
    visibility: hidden;
  }
  
  @include r(1300) {
    width: 150px;
    .b-service__img {
      width: 150px;
      height: 150px;
    }
  }
  @include r(959) {
    width: 100px;
    .b-service__img {
      width: 100px;
      height: 100px;
      img {
        max-width: 50%;
      }
    }
    .b-service__title {
      font-size: 16px;
    }
  }
  &:nth-of-type(even) {
    .b-service__img {
      &::after {
        top: auto;
        bottom: 0;
        transform: rotateX(180deg);
      }
    }
  }
  &:last-of-type {
    .b-service__img {
      &::after {
        content: none;
      }
    }
  }
  &__img {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 195px;
    height: 195px;
    margin-bottom: 15px;
    border: 1px solid #d0d0d0;
    border-radius: 50%;
    &::after {
      content: '';
      position: absolute;
      right: -50px;
      top: 0;
      width: 60px;
      height: 30px;
      background: url($img + 'icons/services-arrow.svg') no-repeat;
      background-size: contain;
      @include r(959) {
        content: none;
      }
    }
  }
}//b-service

.b-responses {
  text-align: center;
  &__title {
    color: #00a651;
    font-size: 30px;
    font-weight: 700;
    text-transform: uppercase;
    @include r(479) {
      font-size: 20px;
    }
  }
  &__list {
    padding: 0 70px 75px;
    @include r(1300) {
      padding: 0 35px 75px;
    }
    .swiper-container {
      padding-bottom: 5px;
    }
  }
}

.b-response {
  display: inline-block;
  padding: 20px 85px 30px;
  max-width: 495px;
  text-align: center;
  transition: all .3s;
  @include r(1300) {
    padding: 20px 15px 30px;
  }
  @include r(959) {
    font-size: 14px;
  }
  &:hover {
    box-shadow: 0 3px 10px 0 rgba(#000, 0.1);
  }
  &__img {
    margin: 0 auto 30px;
    width: 132px;
    height: 132px;
    background-color: #f7f7f7;
    border: 10px solid #f7f7f7;
    border-radius: 50%;
    box-shadow: 0 3px 10px 0 rgba(#000, 0.1);
    overflow: hidden;
    img {
      display: block;
      margin: 0 auto;
    }
  }
  &__author {
    margin-bottom: 30px;
    color: #00a651;
    font-weight: 500;
    text-transform: uppercase;
  }
  &__text {
    opacity: 0.9;
    color: #000;
    line-height: 1.56;
  }
}//b-response

.b-special {
  padding: 50px 0;
  color: #fff;
  text-align: center;
  background: url($img + 'special-bg.jpg') no-repeat;
  background-size: cover;
  &__title {
    margin-bottom: 20px;
    font-size: 30px;
    font-weight: 700;
    text-transform: uppercase;
  }
  &__subtitle {
    margin-bottom: 40px;
    opacity: 0.8;
    font-size: 20px;
    @include r(479) {
      margin-bottom: 20px;
    }
  }
}//b-special

.b-callback {
  max-width: 780px;
  margin: 0 auto;
  .b-field {
    color: #fff;
    margin-bottom: 50px;
    @include r(479) {
      margin-bottom: 25px;
    }
    input {
      display: block;
      padding: 5px 0;
      background: transparent;
      border: none;
      border-bottom: 1px solid #fff;
      @include placeholder {
        color: #fff;
        text-transform: uppercase;
      }
    }
  }
  .btn {
    min-width: 480px;
    max-width: 100%;
    font-size: 20px;
    @include r(767) {
      min-width: 0;
    }
    @include r(479) {
      width: 100%;
      font-size: 18px;
    }
  }
}//b-callback

.b-map {
  height: 700px;
  @include r(959) {
    height: 400px;
  }
}

.animated {
  animation-duration: 1s;
  animation-fill-mode: both;
  visibility: visible !important;
}

@keyframes zoomIn {
  from {
    opacity: 0;
    transform: scale3d(.3, .3, .3);
  }

  50% {
    opacity: 1;
  }
}

.zoomIn {
  animation-name: zoomIn;
}